define([
], function (
) {
    /*
    * @description: Objeto que contiene los componentes html para ejecutar 
         el proceso de asignaci√≥n de ubicacion de predios
    */

    const LandProcess = {
        title: null, // @params
        type: null, // @params
        id: `SendData${this.type}Cm`, // @params

        stringToObjectHtml(htmlString) {
            const htmlObject = dojo.create('div', { innerHTML: htmlString });
            return htmlObject.firstChild;
        },
        renderButtonProcess() {
            const renderString = `<div class="ctnSendDataClsCm">
                        <button id="${this.id}" class="btnSendClsCm">
                            ${this.title}
                        </button>
                    </div>`;

            return this.stringToObjectHtml(renderString);
        },
    };
    return LandProcess;
});